// bootstrap utilities
@import './bootstrap_breakpoints';
@import './bootstrap_colors';

// custom utilities
@import './layout_utilities';

// additional theme colors
$accent: #5376F5;
$theme-colors: map-merge(( accent: $accent), $theme-colors);

// components
@import './comp_form/layout';
@import './comp_form/copy';
@import './callout/callout';
@import './bar_callout/bar_callout';
@import './sidebar/sidebar';
@import './slides/slides';


// copy
.topic {
    padding-top: 40px; // border: 1px solid red;
}



// js-lab
div.js-lab {
    @include full-width;
    height: 500px;
    outline: 1px solid gray;
}

iframe.js-lab {
    border: none;
    width: 100%;
    height: 100%;
}



